
@charset "UTF-8";
.page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.page-fixed {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

/*-------------------*  $ header
\*-------------------*/
.student__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 1.33rem;
  color: #2A2A2A;
  background: #EDF2F6;
  /* box-shadow: 0 4px 6px rgba(0,0,0, 0.2); */
}
.student__header .student__header--back, .student__header .student__header--more {
    width: 1.0rem;
}
.student__header .header-title {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    color: #2a2a2a;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.student__header .student__header--more {
    position: relative;
    width: 1.2rem;
}
.student__header .student__header--more .more-actions {
      position: absolute;
      top: 1.33rem;
      right: 0.1rem;
      padding: 0 0.28rem;
      width: 4.0rem;
      /*height: 3.12rem;*/
      color: #fff;
      background: rgba(51, 51, 51, 0.9);
      border-radius: 0.106667rem;
      -webkit-animation-duration: 0.3s;
              animation-duration: 0.3s;
}
.student__header .student__header--more .more-actions .line {
        border-bottom: 1px solid #C8C8C8;
}
.student__header .student__header--more .more-actions .action {
        padding: 0.266667rem 0;
        line-height: 0.933333rem;
        text-align: center;
}
.student__header .student__header--more .more-actions .action .iconfont {
          padding-right: 0.186667rem;
          vertical-align: -0.08rem;
}
.student__header .student__header--more .more-actions:before {
      position: absolute;
      top: -0.399rem;
      right: 0.28rem;
      content: '';
      border: 0.2rem solid rgba(51, 51, 51, 0.9);
      border-color: transparent transparent rgba(51, 51, 51, 0.9) transparent;
}

/*-------------------*  $ header
\*-------------------*/
.student__tabs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 1rem;
  background: #fff;
  -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}
.student__tabs .tab-item {
    height: 100%;
    line-height: 1rem;
    padding: 0 0.5rem;
}
.student__tabs .curr {
    color: #639EF4;
    border-bottom: 4px solid #639EF4;
}

/*-------------------*  $ 时间轴 列表
\*-------------------*/
.student__timeline-wrapper {
  position: absolute;
  top: 2.33rem;
  left: 0;
  right: 0;
  bottom: 0;
  /*overflow-y: scroll;*/
  -webkit-overflow-scrolling: touch;
}
.student__timeline {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: reverse;
      -ms-flex-direction: column-reverse;
          flex-direction: column-reverse;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  min-width: 10rem;
  width: 100%;
}
.student__timeline .timeline-wrapper {
    width: 100%;
}
.student__msg {
  position: absolute;
  top: 2.5rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin: auto;
  padding: 0 .4rem;
  height: .8rem;
  line-height: .8rem;
  text-align: center;
  color: #fff;
  background: rgba(51, 51, 51, 0.6);
  border-radius: .4rem/50%;
}

/*-------------------*  $ 消息box 超过图片弹层
\*-------------------*/
.student__msgbox {
  z-index: 3;
  position: fixed;
  top: 0;
  width: 100%;
}

/*-------------------*  $ 网络超时重连
\*-------------------*/
.student__net-mask {
  z-index: 2;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.55);
}
.student__net-mask .content-block {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    color: #fff;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.student__net-mask .content-block .connect-btn {
      margin: 0.4rem auto;
      width: 2.666667rem;
      height: 0.933333rem;
      line-height: 0.933333rem;
      color: #fff;
      border: 1px solid #fff;
      border-radius: 10px;
}



